
















































































































































.message-table {
    margin-bottom: 40px
}

.form-inline.submit {
    position: fixed;
    bottom: 25px;
    width: 260px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}

.form-inline.submit input {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}

.form-inline.submit .message {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
}

.userchoice {
    position: fixed;
    top: 90px;
    /*background-color: red;*/
    height: 40px;
    width: 300px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    color: white;
    text-align: center;
    font-size: 20px
}

.userchoice div {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1
}

.userchoice div:hover {
    cursor: pointer;
    background: black;
    background-color: rgba(0, 0, 0, 0.1)
}

.users {
    margin-top: 50px;
    /*background-color: #a8bcf5;*/
    /*height: 100%;*/
    /*width: 350px;*/
}

.people {
    margin: 20px;
    height: 60px
}

.people:hover {
    cursor: pointer;
    background: black;
    background-color: rgba(0, 0, 0, 0.1)
}

.people .img {
    width: 50px;
    height: 50px;
    overflow: hidden;
    border-radius: 50%;
    border: 2px solid #fff;
    box-shadow: 0 3px 2px rgba(0, 0, 0, .4);
}

.people .name {
    position: relative;
    left: 80px;
    top: -50px;
    font-size: 20px;
    width: 200px;
}

.people .title {
    position: relative;
    left: 80px;
    top: -50px;
    font-size: 15px;
    width: 200px;
}

.people .online {
    position: relative;
    left: 250px;
    top: -85px;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    border: 2px solid #fff;
    box-shadow: 0 3px 2px rgba(0, 0, 0, .4);
    background-color: grey;
}

.people .online.active {
    background-color: green;
}

img {
    width: 100%
}

#page3-content {
    overflow: auto;
    height: 100%;
}

@media (min-width: 736px) {
    .form-inline .form-control {
        /*display: inline-block;*/
        /*改成width: 0  可以取消掉 bootstrap中的width属性*/
        width: 0;
        /*vertical-align: middle;*/
    }
    .form-inline.submit {
        position: fixed;
        bottom: 50%;
        margin-bottom: -340px;
        width: 300px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
    }
    .userchoice {
        position: fixed;
        top: 50%;
        margin-top: -275px;
        /*background-color: red;*/
        height: 40px;
        width: 300px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        color: white;
        text-align: center;
        font-size: 20px
    }
}



