







































































.mobile {
    /*margin: 50px auto;*/
    /*width: 100%;*/
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background: url(http://www.bz55.com/uploads/allimg/151030/139-151030111Q4.jpg) center no-repeat;
    position: absolute;
    /*解决内容撑大的问题*/
    overflow: hidden;
}
@media (min-width: 736px){
    .mobile{
        height: 736px;
        width: 414px;
        left: 50%;
        top: 50%;
        margin-top: -368px;
        margin-left:-207px;
        position: absolute;;
        overflow: hidden;
        box-shadow: 0 12px 15px 0 rgba(0, 0, 0, .24), 0 17px 50px 0 rgba(0, 0, 0, .19);
    }
    .sidebar-overlay {
    top: 50%;
    /*bottom: 0px;*/
    left: 50%;
    /*right: 0px;*/
    background-color: black;
    position: absolute;
    opacity: 0.5;
    z-index: 5;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: all .5s ease-in-out 0s;
    transition: all .5s ease-in-out 0s;
    }
}

.header {
    position: absolute;
    margin-left: 10%;
    margin-top: 30px;
    width: 80%;
    height: 100px;
    /*background-color: white;*/
    font-size: 30px;
    color: #fff;
    /*z-index: 2;*/
}

.header .sidebar-icon:hover {
    cursor: pointer;
    background: black;
    background-color: rgba(0, 0, 0, 0.1)
}

.sidebar {}

.sidebar.active .sidebar-content,
.sidebar.active .sidebar-overlay {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.sidebar-overlay {
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: black;
    position: absolute;
    opacity: 0.5;
    z-index: 5;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: all .5s ease-in-out 0s;
    transition: all .5s ease-in-out 0s;
}

.sidebar-content {
    position: absolute;
    top: 0px;
    width: 80%;
    height: 100%;
    background-color: white;
    z-index: 100;
    font-size: 20px;
    padding: 30px;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: all .3s ease-in-out 0s;
    transition: all .3s ease-in-out 0s;
}

.nav-left {
    position: absolute;
    top: 150px;
    line-height: 50px;
}

.nav-left a {
    display: block;
}

.nav-left span {
    font-size: 30px;
    padding: 10px;
}

.nav {
    position: absolute;
    right: 20px;
    bottom: 20px;
    width: 50px;
    height: 50px;
    background-color: #f23363;
    line-height: 50px;
    border-radius: 50%;
    -webkit-transition: all .1s ease-in-out 0s;
    transition: all .1s ease-in-out 0s;
}

.nav:after {
    position: absolute;
    content: '';
    right: 0px;
    bottom: 0px;
    width: 50px;
    height: 50px;
    background-color: #f23363;
    line-height: 50px;
    border-radius: 50%;
    -webkit-transition: all .1s ease-in-out 0s;
    transition: all .1s ease-in-out 0s;
    z-index: 2;
}

.musk {
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    font-size: 30px;
    border-radius: 50%;
    -webkit-transition: all .3s cubic-bezier(.68, 1.55, .265, 1);
    transition: all .3s cubic-bezier(.68, 1.55, .265, 1);
    text-align: center;
    z-index: 4;
}
.musk:hover {
    cursor: pointer;
}

.nav.active .musk {
    background: #cf0e3f;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
}


/*实现增加属性后 动画*/


/*用after 实现的是 背景动的效果*/

.nav.active:after {
    /*position: absolute;
    content: '';*/
    right: -150px;
    bottom: -150px;
    width: 350px;
    height: 350px;
    /*background-color: #f23363;
    line-height: 50px;
    border-radius: 50%;
    z-index: 1;*/
}

.nav-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    color: #fff;
    font-size: 30px;
    -webkit-transition: all .3s cubic-bezier(.68, 1.55, .265, 1);
    transition: all .3s cubic-bezier(.68, 1.55, .265, 1);
    text-align: center;
    z-index: 1;
}

.nav-item:hover {}

.nav.active .nav-item {
    z-index: 3;
}

.nav.active .nav-count-1 {
    -webkit-transform: translate(10px, -130px);
            transform: translate(10px, -130px);
}

.nav.active .nav-count-2 {
    -webkit-transform: translate(-42px, -117px);
            transform: translate(-42px, -117px)
}

.nav.active .nav-count-5{
    -webkit-transform: translate(-88px, -88px);
            transform: translate(-88px, -88px)
}

.nav.active .nav-count-3 {
    -webkit-transform: translate(-117px, -42px);
            transform: translate(-117px, -42px);
}

.nav.active .nav-count-4 {
    -webkit-transform: translate(-130px, 10px);
            transform: translate(-130px, 10px)
}

.routerview{
  position: absolute;
    top: 120px;
    bottom: 90px;
    margin-left: 8%;
    margin-right: 8%;
    overflow: auto;
    overflow-x: hidden;
    width: 84%;
}




